@use '../../../assets/styles/abstracts/colors' as colors;

.investors {

  &__lead {
    padding: 20px 0 0;

    &-container {
      display: flex;
      justify-content: space-between;
    }

    &-content-container {
      max-width: 523px;
      margin-right: 20px;
    }

    &-title {
      margin: 80px 0 40px;
      padding: 0;
    }
    
    &-link {
      display: block;
      font-size: 24px;
      line-height: 29px;
      color: colors.$blue;
      margin: 40px 0 0;
      text-transform: lowercase;

      &::after {
        display: inline-block;
        content: '\1F783';
        font-size: 18px;
        font-weight: 700;
        transform: translateY(-2px);
        margin-left: 10px;
      }
    }

    &-image-container {
      width: 100%;
      background: url("../../../assets/images/investors/lead-circle.svg");
      background-repeat: no-repeat;
      background-position: center bottom;
      background-size: contain;
      display: flex;
      justify-content: center;
      align-items: flex-end;
    }

    &-image-wrapper {
      max-width: 347px;
    }

    &-image {
      width: 100%;
      object-fit: contain;
      transform: translateY(90px);
    }
  }

  &__content {
    padding: 200px 0;

    &-block {
      margin-bottom: 200px;
      
      &:last-of-type {
        margin-bottom: 0;
      }
    }

    &-title {
      &_first {
        margin-bottom: 100px;
      }

      &_second {
        margin-bottom: 55px;
      }

      &_third {
        margin-bottom: 40px;
      }

      &_last {
        font-size: 32px;
        line-height: 39px;
        margin-bottom: 55px;
        text-transform: none;
      }
    }

    &-card-container {
      display: flex;
      justify-content: space-between;
    }

    &-card-image-container {
      min-width: 440px;
    }

    &-card-image {
      width: 100%;
      object-fit: contain;
    }

    &-card-info-container {
      margin-left: 110px;
      max-width: 100%;
    }

    &-card-info-list {
      font-size: 18px;
      line-height: 22px;
      color: #222222;
      margin: 0;
      padding: 0;

      &-item {
        margin: 0 0 25px;
        padding: 0 0 0 20px;
      }
    }

    &-card-info {
      display: inline-block;
      font-size: 32px;
      line-height: 39px;
      font-weight: 700;
      padding: 20px 0 0;
      color: colors.$blue;
      white-space: nowrap;
    }

    &-note {
      font-size: 14px;
      line-height: 17px;
      color: rgba(34, 34, 34, 0.6);
      max-width: 586px;
    }

    &-flexbox-container {
      display: flex;
      justify-content: space-between;
      gap: 110px;
      margin-bottom: 60px;
    }

    &-flexbox {
      width: 50%;
    }

    &-subtitle {
      font-size: 32px;
      line-height: 39px;
      font-weight: 700;
      letter-spacing: -0.02em;
      color: #222222;
      margin: 0 0 20px;
      padding: 0;
      text-transform: lowercase;
    }

    &-text {
      font-size: 18px;
      line-height: 22px;
      margin: 0 0 20;
      padding: 0;
      color: #222222;
      max-width: 690px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }

    &-quote {
      max-width: 894px;
    }

    &-button {
      font-size: 16px;
      line-height: 19px;
      font-weight: 700;
      background-color: transparent;
      outline: none;
      border: none;
      color: colors.$blue;
      cursor: pointer;
    }

    &-list {
      font-size: 18px;
      line-height: 22px;
      color: #222222;
      margin: 0 0 0 20px;
      padding: 0;

      &-item {
        margin: 0 0 15px;

        &:last-of-type {
          margin-bottom: 55px;
        }
      }
    }
  }
}